<template>
    <div class="baseoutputParamsp-modal">
        <a-modal :title="title" :visible="regVisible" :mask=false :maskClosable=false @cancel="handleCancel"  :width="600" class="modalCls">
            <a-form-model ref="baseRegForm" :model="baseRegForm" :rules="rulesForm">
                <a-form-model-item label="内置函数名称" prop="name">
                    <a-input v-model="baseRegForm.name" placeholder="请输入名称"></a-input>
                </a-form-model-item>
                <a-form-model-item label="内置函数输入参数" prop="inputParams">
                    <a-input v-model="baseRegForm.inputParams" placeholder="请输入内置函数输入参数"></a-input>
                </a-form-model-item>
                <a-form-model-item label="内置函数输出参数" prop="outputParams">
                    <a-input v-model="baseRegForm.outputParams" placeholder="请输入内置函数输出参数"></a-input>
                </a-form-model-item>
                <a-form-model-item label="内置函数描述" prop="description">
                    <a-textarea v-model="baseRegForm.description" placeholder="请输入内置函数描述信息" rows="4"></a-textarea>
                </a-form-model-item>

            </a-form-model>
            <template slot="footer">
                <a-button type="link" ghost @click="handleCancel">取消</a-button>
                <a-button type="primary" @click="handleOk">提交</a-button>
            </template>

        </a-modal>

    </div>
</template>

<script>
  export default {
    name: "baseoutputParamsp-modal",
    props:{
      title:{
        type: String,
        default: "添加/编辑内置函数",
      },
      regVisible:{
        type:Boolean,
        default: false,
        required: true,
      },
      // 0为添加，1为编辑
      regType:{
        type:Number,
        default:0,
        required: true,
      },
      formContent: {
        type:Object,
        default:function () {
          return{
            name:'',
            inputParams:'',
            outputParams:'',
            description:'',
            isEnabled:false,
            gmtCreate:'',
            gmtModified:'',
          }
        }
      }


    },
    data(){
      return{
        baseRegForm:{
          name:'',
          inputParams:'',
          outputParams:'',
          description:''
        },
        rulesForm:{
          name:[{ required: true, message: "请输入内置函数名称",trigger:'blur'}],
          inputParams:[{required: true,message:'请输入内置函数类型',trigger: 'blur'}],
          outputParams:[{required: true,message:'请输入内置函数表达式',trigger: 'blur'}]
        },
      }

    },
    methods:{
      handleOk(){
        this.$refs.baseRegForm.validate(valid =>{
          if (valid){
              let form = Object.assign({},this.baseRegForm)
            this.$emit('handleOk',{formData:form,regType:this.regType})
          } else {
            this.$message.warning('输入有误，请检查');
            return false
          }
        })
      },
      handleCancel(){
        this.$emit('handleCancel','点击取消')
      }


    },
    watch:{
      formContent:{
        handler:function(newVal){
          Object.assign(this.baseRegForm,newVal);
        },
        deep:true
      }
    }
  }
</script>

<style scoped>

</style>