<template>
    <div class="baseRegExp pageSpace h100">
        <my-breadcrumb></my-breadcrumb>
        <div class="pageContent">
            <a-layout-content>
                <div class="baseRegExp">
                    <div class="tableTopTools clearfix">
                        <p class="toolName fl">工具栏：</p>
                        <div class="buttons fl">
                            <a-button type="primary" @click="addBaseReg">
                                <a-icon type="plus"></a-icon>添加内置函数
                            </a-button>
                        </div>
                    </div>
                    <div class="tableContent">
                        <a-table :columns="columns" :data-source="tableData" rowKey="id" :pagination="false">
                            <span slot="action" slot-scope="text,record">
                                 <a class="t-optColor-normal" @click="editBaseReg(record)">编辑</a>
                                 <a class="t-optColor-warning" @click="deleteBaseReg(record)">删除</a>
                            </span>
                            <span slot="gmtCreate" slot-scope="text,record">
                                <span>{{record.gmtCreate | dateFormatter(1)}}</span>
                            </span>
                            <span slot="gmtModified" slot-scope="text,record">
                                <span>{{record.gmtModified | dateFormatter(1)}}</span>
                            </span>
                        </a-table>
                        <div class="ar" style="margin-top: 20px">
                            <a-pagination v-model="curPage" :total="total" :page-size="pageSize" @change="pageChange"></a-pagination>
                        </div>
                    </div>
                    <builtFunModal :regVisible="regVisible" :regType="regType" :formContent="formContent" @handleOk="handleOk"
                                     @handleCancel="handleCancel">
                    </builtFunModal>
                </div>
            </a-layout-content>

        </div>
    </div>
</template>
<script>
    import builtFunModal from '@/components/myModal/builtFun-modal'
    export default {
        name:'baseRegExp',
        components: { builtFunModal },
        data(){
            return{
                curPage:1,
                total:0,
                pageSize:10,
                regVisible:false, // 模态框状态
                regType:0,  // 添加或编辑状态

                columns: [
                    {
                        dataIndex: 'name',
                        title: '名称'
                    },
                    {
                        dataIndex: 'inputParams',
                        title: '输入参数'
                    },
                    {
                        dataIndex: 'outputParams',
                        title: '输出参数',
                        ellipsis: true,
                    },
                    {
                        dataIndex: 'description',
                        title: '描述',
                        ellipsis: true
                    },
                    {
                        dataIndex: 'gmtCreate',
                        title: '创建时间',
                        scopedSlots: {customRender: 'gmtCreate'}
                    },
                    {
                        dataIndex: 'gmtModified',
                        title: '更新时间',
                        scopedSlots: {customRender: 'gmtModified'}
                    },
                    {
                        dataIndex: 'action',
                        title: "操作",
                        scopedSlots: {customRender: "action"},
                    }
                ],
                tableData:[],
                formContent:{
                    name:'',
                    category:'',
                    regex:'',
                    description:'',
                    creator:'',
                    gmtCreate:'',
                    gmtModified:'',
                },

            }
        },
        methods:{
            // 查询
            getBaseRegExpList(){
                let params ={
                    page:this.curPage,
                    size:this.pageSize,
                };
                this.$API.searchBuiltFunction(params).then(res =>{
                    if (res.code === 1000){
                        this.tableData =res.data;
                        this.total = res.total;
                    } else {
                        this.tableData = [];
                        this.$message.warning('暂无数据')
                    }
                })

            },
            // 添加
            addBaseReg(){
                this.resetContent();
                this.regVisible = true;
                this.regType = 0;
            },
            // 提交
            handleOk(val){
                if (val.regType){
                    this.$API.updateBuiltFunction(Object.assign({},val.formData,{id:this.formContent.id})).then(res =>{
                        if (res.code === 1000){
                            this.$message.success('内置函数更新成功');
                            this.regVisible = false;
                            this.getBaseRegExpList()
                        } else {
                            this.$message.warning('内置函数更新失败')
                        }
                    })
                } else {
                    let params = {
                        name:val.formData.name,
                        description:val.formData.description,
                        inputParams:val.formData.inputParams,
                        outputParams:val.formData.outputParams
                    };
                    this.$API.addBuiltFunction(params).then(res =>{
                        if (res.code === 1000){
                            this.$message.success("内置函数添加成功");
                            this.regVisible = false;
                            this.getBaseRegExpList()
                        } else {
                            this.$message.warning('内置函数添加失败')
                        }
                    })

                }
            },
            // 取消
            handleCancel(){
                this.regVisible = false;
                this.resetContent()
            },

            // 编辑
            editBaseReg(column){
                this.formContent = column;
                this.regType = 1;
                this.regVisible = true;
            },
            // 删除
            deleteBaseReg(row){
                const that = this;
                    that.$confirm({
                        title:'确认删除对话框',
                        okText:'确认',
                        cancelText:'取消',
                        content:'该条数据正在使用，确定要删除吗',
                        onOk(){
                            that.$API.deleteBuiltFunction(row.id).then(res =>{
                                if (res.code === 1000){
                                    that.$message.success('删除成功');
                                    that.getBaseRegExpList()
                                } else {
                                    that.$message.warning('删除失败')
                                }
                            })
                        }

                    })
            },
            pageChange(){
                this.getBaseRegExpList()
            },
            // 重置form表单
            resetContent() {
                this.formContent = {
                    name:'',
                    inputParams:'',
                    outputParams:'',
                    description:''
                };
            },


        },
        created() {
            this.getBaseRegExpList()
        }

    }
</script>
<style>

</style>